.descriptionbox {
  margin: 120px 100px;
}

.descriptionbox-navigator {
  display: flex;
}

.descriptionbox-nav-box {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  font-weight: 600;
  width: 171px;
  height: 70px;
  border: 1px solid #d0d0d0;
}

.descriptionbox-nav-box.fade {
  background: #FBFBFB;
  color: #555;
}

.descriptionbox-description {
  display: flex;
  flex-direction: column;
  text-align: start;
  gap: 25px;
  border: 1px solid #D0D0D0;
  padding: 48px;
  padding-bottom: 70px;
}

@media (max-width: 559px) {
  .descriptionbox {
    margin: 70px 30px;
  }

  .descriptionbox-description {
    padding: 28px;
  padding-bottom: 50px;
  }
}